"use client";

import React, { useState, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useSession } from "next-auth/react";
import EditProfileModal from "@/components/EditProfileModal";

interface UserProfile {
  username: string;
  displayName: string;
  bio: string;
  followers: number;
  following: number;
  totalViews: number;
  joinedDate: string;
  isVerified: boolean;
  phoneNumber?: string;
  googleId?: string;
  email?: string;
}

interface UserContent {
  id: string;
  title: string;
  type: "video" | "photo" | "music";
  thumbnail: string;
  views: number;
  uploadDate: string;
  duration?: string;
}

const mockProfile: UserProfile = {
  username: "creator_user",
  displayName: "Creative User",
  bio: "Content creator passionate about sharing knowledge and entertainment. Join me on this amazing journey! üöÄ",
  followers: 12500,
  following: 234,
  totalViews: 1250000,
  joinedDate: "2023-06-15",
  isVerified: true
};

const mockContent: UserContent[] = [
  {
    id: "1",
    title: "Amazing Gaming Highlights",
    type: "video",
    thumbnail: "https://via.placeholder.com/300x200/1a1a1a/ffffff?text=Gaming+Video",
    views: 45000,
    uploadDate: "2024-01-10",
    duration: "12:34"
  },
  {
    id: "2",
    title: "Beautiful Sunset Photography",
    type: "photo",
    thumbnail: "https://via.placeholder.com/300x200/2a2a2a/ffffff?text=Sunset+Photo",
    views: 23000,
    uploadDate: "2024-01-08"
  },
  {
    id: "3",
    title: "Chill Beats Mix",
    type: "music",
    thumbnail: "https://via.placeholder.com/300x200/3a3a3a/ffffff?text=Music+Mix",
    views: 67000,
    uploadDate: "2024-01-05",
    duration: "45:20"
  },
  {
    id: "4",
    title: "Tutorial: Web Development",
    type: "video",
    thumbnail: "https://via.placeholder.com/300x200/4a4a4a/ffffff?text=Tutorial+Video",
    views: 89000,
    uploadDate: "2024-01-03",
    duration: "28:15"
  },
  {
    id: "5",
    title: "Street Art Collection",
    type: "photo",
    thumbnail: "https://via.placeholder.com/300x200/5a5a5a/ffffff?text=Street+Art",
    views: 34000,
    uploadDate: "2024-01-01"
  },
  {
    id: "6",
    title: "Electronic Dance Track",
    type: "music",
    thumbnail: "https://via.placeholder.com/300x200/6a6a6a/ffffff?text=EDM+Track",
    views: 56000,
    uploadDate: "2023-12-28",
    duration: "4:32"
  }
];

export default function ProfilePage() {
  const { data: session, status } = useSession();
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [content, setContent] = useState<UserContent[]>([]);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState<"all" | "video" | "photo" | "music">("all");
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const router = useRouter();

  useEffect(() => {
    const loadProfile = async () => {
      setLoading(true);
      await new Promise(resolve => setTimeout(resolve, 800));
      
      if (session?.user) {
        setProfile({
          username: session.user.email?.split('@')[0] || 'user',
          displayName: session.user.name || 'User',
          bio: `Welcome to my profile! Connected via Google: ${session.user.email}`,
          followers: 12500,
          following: 234,
          totalViews: 1250000,
          joinedDate: new Date().toISOString().split('T')[0],
          isVerified: true,
          googleId: session.user.email || undefined,
          email: session.user.email || undefined
        });
        setContent(mockContent);
      } else {
        setProfile(null);
      }
      setLoading(false);
    };

    loadProfile();
  }, [session]);

  const handleSaveProfile = (updatedProfile: {
    displayName: string;
    username: string;
    bio: string;
  }) => {
    if (profile) {
      setProfile({
        ...profile,
        ...updatedProfile
      });
    }
  };

  const handleLogout = () => {
    // This will be handled by NextAuth signOut
    router.push('/');
  };

  const formatNumber = (num: number) => {
    if (num >= 1000000) {
      return `${(num / 1000000).toFixed(1)}M`;
    } else if (num >= 1000) {
      return `${(num / 1000).toFixed(1)}K`;
    }
    return num.toString();
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    });
  };

  const filteredContent = activeTab === "all" 
    ? content 
    : content.filter(item => item.type === activeTab);

  const getContentIcon = (type: string) => {
    switch (type) {
      case "video": return "üé•";
      case "photo": return "üì∏";
      case "music": return "üéµ";
      default: return "üìÑ";
    }
  };

  // Removed handleFollow as it's not needed for profile page

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
          <p className="mt-4 text-muted-foreground">Loading profile...</p>
        </div>
      </div>
    );
  }

  if (!session) {
    return (
      <div className="container mx-auto px-4 py-8 max-w-md">
        <div className="bg-card border border-border rounded-lg p-8">
          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold mb-2">Welcome to RICO CRASRLA LIVE</h1>
            <p className="text-muted-foreground">Sign in to access your profile</p>
          </div>

          <div className="space-y-4">
            <button
              onClick={() => router.push('/auth/login')}
              className="w-full px-4 py-3 bg-primary text-primary-foreground rounded-lg font-medium hover:bg-primary/90 transition-colors"
            >
              Login with Phone Number
            </button>

            <button
              onClick={() => router.push('/auth/login')}
              className="w-full px-4 py-3 border border-border rounded-lg font-medium hover:bg-accent transition-colors flex items-center justify-center gap-2"
            >
              <span className="text-xl">üîç</span>
              Login with Google
            </button>

            <div className="text-center">
              <p className="text-sm text-muted-foreground">
                Don't have an account?{" "}
                <Link href="/auth/register" className="text-primary hover:underline">
                  Sign up
                </Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-6xl">
      {/* Profile Header */}
      <div className="bg-card border border-border rounded-lg p-8 mb-8">
        <div className="flex flex-col md:flex-row items-center md:items-start gap-6">
          {/* Avatar */}
          <div className="w-32 h-32 bg-primary/20 rounded-full flex items-center justify-center flex-shrink-0">
            <span className="text-primary font-bold text-4xl">
              {profile!.displayName.charAt(0)}
            </span>
          </div>

          {/* Profile Info */}
          <div className="flex-1 text-center md:text-left">
            <div className="flex flex-col md:flex-row md:items-center gap-3 mb-4">
              <h1 className="text-3xl font-bold text-card-foreground">
                {profile!.displayName}
              </h1>
              {profile!.isVerified && (
                <span className="inline-flex items-center gap-1 px-3 py-1 bg-blue-600/10 text-blue-600 rounded-full text-sm font-medium">
                  ‚úì Verified
                </span>
              )}
            </div>

            <p className="text-muted-foreground mb-4 text-lg">
              @{profile!.username}
            </p>

            <p className="text-card-foreground mb-6 max-w-2xl leading-relaxed">
              {profile!.bio}
            </p>

            {/* Stats */}
            <div className="flex flex-wrap justify-center md:justify-start gap-6 mb-6">
              <div className="text-center">
                <div className="text-2xl font-bold text-card-foreground">
                  {formatNumber(profile!.followers)}
                </div>
                <div className="text-sm text-muted-foreground">Followers</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-card-foreground">
                  {formatNumber(profile!.following)}
                </div>
                <div className="text-sm text-muted-foreground">Following</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-card-foreground">
                  {formatNumber(profile!.totalViews)}
                </div>
                <div className="text-sm text-muted-foreground">Total Views</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-card-foreground">
                  {content.length}
                </div>
                <div className="text-sm text-muted-foreground">Content</div>
              </div>
            </div>

            {/* Action Buttons */}
            <div className="flex flex-wrap justify-center md:justify-start gap-3">
              <button
                onClick={() => setIsEditModalOpen(true)}
                className="px-6 py-3 border border-border rounded-lg font-medium hover:bg-accent transition-colors"
              >
                Edit Profile
              </button>
            </div>

            {/* Login Method */}
            <div className="mt-4 text-sm text-muted-foreground">
              Joined {formatDate(profile!.joinedDate)}
              {profile!.email && (
                <p>Google Account: {profile!.email}</p>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Content Tabs */}
      <div className="mb-6">
        <div className="flex flex-wrap gap-2 justify-center md:justify-start">
          {[
            { id: "all", label: "All Content", count: content.length },
            { id: "video", label: "Videos", count: content.filter(c => c.type === "video").length },
            { id: "photo", label: "Photos", count: content.filter(c => c.type === "photo").length },
            { id: "music", label: "Music", count: content.filter(c => c.type === "music").length }
          ].map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                activeTab === tab.id
                  ? "bg-primary text-primary-foreground"
                  : "bg-secondary text-secondary-foreground hover:bg-secondary/80"
              }`}
            >
              {tab.label} ({tab.count})
            </button>
          ))}
        </div>
      </div>

      {/* Content Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredContent.map((item) => (
          <div
            key={item.id}
            className="group bg-card border border-border rounded-lg overflow-hidden hover:border-primary/50 transition-all duration-300 hover:shadow-lg"
          >
            <div className="relative">
              <img
                src={item.thumbnail}
                alt={item.title}
                className="w-full h-48 object-cover group-hover:scale-105 transition-transform duration-300"
              />
              
              {/* Content Type Badge */}
              <div className="absolute top-3 left-3">
                <span className="px-2 py-1 bg-black/70 text-white text-xs font-medium rounded-full">
                  {getContentIcon(item.type)} {item.type.toUpperCase()}
                </span>
              </div>

              {/* Duration (for video/music) */}
              {item.duration && (
                <div className="absolute bottom-3 right-3">
                  <span className="px-2 py-1 bg-black/70 text-white text-xs rounded-full">
                    {item.duration}
                  </span>
                </div>
              )}
            </div>
            
            <div className="p-4">
              <h3 className="font-semibold text-card-foreground mb-2 line-clamp-2 group-hover:text-primary transition-colors">
                {item.title}
              </h3>
              
              <div className="flex items-center justify-between text-sm text-muted-foreground">
                <span>{formatNumber(item.views)} views</span>
                <span>{formatDate(item.uploadDate)}</span>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* No Content */}
      {filteredContent.length === 0 && (
        <div className="text-center py-12">
          <div className="text-6xl mb-4">üì≠</div>
          <h3 className="text-xl font-semibold mb-2">No content found</h3>
          <p className="text-muted-foreground">
            No {activeTab === "all" ? "" : activeTab} content available yet.
          </p>
        </div>
      )}

      {/* Upload CTA */}
      <div className="text-center mt-16 py-12 bg-secondary/30 rounded-lg">
        <h2 className="text-2xl font-bold mb-4">Share Your Creativity</h2>
        <p className="text-muted-foreground mb-6 max-w-md mx-auto">
          Upload your videos, photos, and music to showcase your talent and build your audience.
        </p>
        <Link 
          href="/upload" 
          className="inline-flex px-6 py-3 bg-primary text-primary-foreground rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          Upload Content
        </Link>
      </div>

      {/* Edit Profile Modal */}
      {profile && (
        <EditProfileModal
          isOpen={isEditModalOpen}
          onClose={() => setIsEditModalOpen(false)}
          currentProfile={{
            displayName: profile.displayName,
            username: profile.username,
            bio: profile.bio,
          }}
          onSave={handleSaveProfile}
        />
      )}
    </div>
  );
}
